import request from '@/assets/js/request'
import qs from 'qs'

export const Subject = {
  // 参与测评状态
  getStatus(data){
    return request({
      url: '/joinInfo',
      method: 'get',
      params: data
    })
  },
  // 获取 题目
  getSubject(data) {
    return request({
      url: '/subject',
      method: 'get',
      params:data
    })
  },
  // 提交测评
  submitSubject(data){
    return request({
      url: '/store',
      method:'post',
      data: qs.stringify(data)
    })
  },
  // 测评结果
  getResult(data){
    return request({
      url: '/result',
      method: 'get',
      params: data
    })
  }
}


