import axios from 'axios'
import notifys from '@/components/Notification/function'

// create an axios instance
const service = axios.create({
  baseURL: '/api/activity/xingce',
  // baseURL: 'https://api.gongkaoleida.com/api/activity/xingce',
  timeout: 10000
})

// request interceptor
service.interceptors.request.use(
  config => {
    return config
  },
  error => {
    console.log(error)
    return Promise.reject(error)
  }
)

// response interceptor
service.interceptors.response.use(
  response => {
    const res = response.data
    if (res.code !== 1) {
      if (res.code === 0) {
        notifys({ content: res.message })
        return false
      }
      return Promise.reject(new Error(res.message || 'Error'))
    } else {
      return res
    }
  },
  error => {
    notifys({ content: error.message })
    return Promise.reject(error)
  }
)

export default service



